/* General Page style-rules */
*{
  font-family: "Source Sans Pro", sans-serif;
  font-size: 16px;
  a:hover{
    text-decoration: none;
  }
}
/* Header and footer are always fixed */
html, body {
  height: 100%;
}
main {
  flex: 1 1 auto;
}
.wrapper {
  min-height: 100%;
  display: flex;
  flex-direction: column;
}
/* Header section */
header{
  background-image: url("../img/header-bg.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: fit-content;
}
.container{
  padding: 0;
  height: 100%;
  display: flex;
  align-items: center;
}
@media (min-width: 1200px){
.container, .container-lg, .container-md, .container-sm, .container-xl {
  max-width: 1170px;
}}
/* Navbar section */
.navbar{
  width: 100%;
  padding: 0;
  margin: 19px 0;
}
.navbar-brand{
  display: flex;
  color: #ffffff;
  text-transform: uppercase;
  padding: 0;
  a{
    font-size: 24px;
    line-height: 62px;
    letter-spacing: 1px;
    color: #ffffff;
    font-family: "Quicksand", sans-serif;
  }
  p{
    opacity: 0.302;
    font-size: 9px;
    letter-spacing: 1px;
    line-height: 10px;
    font-weight: 400;
    margin: 3px 0 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
.navbar-nav{
  margin: 0 2px 8px 0;
}
.nav-item a{
  font-size: 16px;
  line-height: 50px;
  color: #fffefe;
  font-weight: 600;
  text-align: center;
  padding: 0 !important;
  padding-left: 22px !important;
  span {
    display: block;
    height: 44px;
    width: fit-content;
    margin: 0 auto;
  }
  span:hover{
    background-image: url("../img/border.png");
    background-repeat: repeat-x;
    background-size: 44px 2px;
    background-position: bottom left;
  }
}
.show {
  align-self: center;
  max-width: 100%;
  margin: 0 auto;
}
.collapsing{
  opacity: 0.1%;
}
.burger-menu__burger {
  transition: transform 0.3s ease-in-out;
  width: 32px;
  height: 3px;
  background-color: #ffffff;
  margin: 5px;
  border-radius: 5px;
}
.burger-menu.open .burger-menu__burger:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}
.burger-menu.open .burger-menu__burger:nth-child(2) {
  opacity: 0;
}
.burger-menu.open .burger-menu__burger:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}
@media (max-width: 576px) {
  .navbar-brand{
    a{
      padding-left: 5px;
    }
  }
}